import {
	Toast,
	Dialog
} from 'vant'
import {
	ajax
} from '../../common/js/util.js'
export default {
	data() {
		return {
			Color: '#DE2910',
			value: '',
			backColor: '#FFFFFF',
			list: [],
			orderId: '',
			airList: [],
			persons: [],
			startDate: '',
			show: false,
			val: '',
			show1: false,
			val1: ''
		}
	},
	onLoad(option) {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
		//初始化详情数据
		this.orderId = option.id
		this.init(option.id)
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		init(id) {
			let self = this
			let datas = {
				orderId: id
			}
			let data = {
				url: '/api/gzh/flightOrderDetail',
				data: datas,
				success: function(res) {
					console.log(res)
					if (res.data.result == 0) {
						self.list = res.data
						for (let i = 0; i < self.list.airlineInfoList.length; i++) {
							self.airList.push(self.list.airlineInfoList[i])
						}
						self.startDate = self.list.airlineInfoList[0].begin_date + ' ' + self.list.airlineInfoList[0].begin_time
						for (let j = 0; j < self.list.passengerList.length; j++) {
							self.persons.push(self.list.passengerList[j])
						}
					}
				}
			}
			ajax(data)
		},
		goPay() {
			let self = this
			let datas = {
				orderId: this.orderId
			}
			let data = {
				url: '/api/gzh/payFlightOrder',
				data: datas,
				success: function(res) {
					console.log(res)
					if (res.data.result == 0) {
						let appId = res.data.body.appId
						let timeStamp = res.data.body.timeStamp
						let nonceStr = res.data.body.nonceStr
						let packages = res.data.body.prepay
						let signType = res.data.body.signType
						let paySign = res.data.body.paySign
						self.onBridgeReady(appId, timeStamp, nonceStr, packages, signType, paySign)
					} else {
						Toast(res.data.resultNote)
					}
				}
			}
			ajax(data)
		},
		backMoney() {
			this.show1 = true
		},
		goCancel() {
			this.show = true
		},
		confirm() {
			if(this.val == ''){
				Toast('备注不能为空')
				return
			}
			
			let self = this
			let datas = {
				OrderNo: this.orderId,
				Remark: this.val
			}
			let data = {
				url: '/api/gzh/cancelFlightOrder',
				data: datas,
				success: function(res) {
					console.log(res)
					if (res.data.result == 0) {
						self.show = false
						Toast('取消成功')
						uni.navigateBack({
							delta: 1
						})
					} else {
						Toast(res.data.resultNote)
					}
				}
			}
			ajax(data)
		},
		confirm1() {
			if(this.val1 == ''){
				Toast('备注不能为空')
				return
			}
			
			let self = this
			let psdIds = ''
			let psgnames = ''
			let air = ''
			for (let i = 0; i < self.persons.length; i++) {
				psdIds += self.persons[i].psgId + ','
				psgnames += self.persons[i].psgName + ','
			}
			for (let i = 0; i < self.airList.length; i++) {
				air += self.airList[i].airlineId + ','
			}
			psdIds = psdIds.substr(0,psdIds.lastIndexOf(','))
			psgnames = psgnames.substr(0,psgnames.lastIndexOf(','))
			air = air.substr(0,air.lastIndexOf(','))
			let datas = {
				OrderNo: this.orderId,
				PsgIDs: psdIds,
				PsgNames: psgnames,
				RefundType: 1,
				AirLineIDs: air,
				Remark: this.val1
			}
			let data = {
				url: '/api/gzh/flightApplyRefund',
				data: datas,
				success: function(res) {
					if (res.data.result == 0) {
						self.show1 = false
						Toast('退票成功')
						uni.navigateBack({
							delta: 1
						})
					} else {
						Toast(res.data.resultNote)
					}
				}
			}
			ajax(data)
		},
		onBridgeReady(appId, timeStamp, nonceStr, packages, signType, paySign) {
			WeixinJSBridge.invoke(
				'getBrandWCPayRequest', {
					'appId': appId,
					'timeStamp': timeStamp,
					'nonceStr': nonceStr,
					'package': packages,
					'signType': signType,
					'paySign': paySign
				},
				function(res) {
					if (res.err_msg === 'get_brand_wcpay_request:ok') {
						Toast('支付成功')
						uni.navigateBack({
							delta: 1
						})
					} else if (res.err_msg === 'get_brand_wcpay_request:cancel') {
						Toast('用户取消支付')
					} else if (res.err_msg === 'get_brand_wcpay_request:fail') {
						Toast('网络异常，请重试')
					}
				}
			)
		}
	}
}
